body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header {
  margin-bottom: 2%;
  margin-top: 3%;
  padding: 0;
  padding-left: 3%;
}

footer p {
  width: 100%;
  margin: 0%;
}

footer {
  margin-top: 2.5%;
  padding-top: 1%;
  padding-bottom: 1%;
  padding-left: 3%;
  background: lightblue;
  width: 100%;

  text-align: center;
  bottom: 0;
}


@media screen and (min-width: 768px) {
  .navBar {
    position: absolute;
    width: 100%;
    left: 0;
    top: 0;
    margin-top: 0;
    background: lightblue;   
  } 
  #navbar-brand {
    font-size: xx-large;
    font-weight: bold;
    
  }
}

  #link {
  padding: 1.25rem;
  }

  #sign-out {
    background-color:#6c757d;
    color: #fff;
    border-color: #6c757d;
    padding: .375rem .75rem;
    border-radius: .25rem;
}


  header, footer, .text {
    width: 75%;
    margin-right: auto;
    margin-left: auto;
    width: 100%;
    padding-left: 5%;
    padding-right: 5%;
  }

  .text {
    padding-top: 3%;
  }

  #sign-out {
    margin-left: 1rem;
  }

  footer, .text {
    padding-left: 2%;
    padding-right: 2%;
  }

  header {
    margin-bottom: 2%;
    margin-top: 6%;
    overflow: hidden;
    padding-left: 1%;
    width: 100%;
    background: white;
  }


ul.mr-auto.navbar-nav.mr-auto {
  margin-left: auto!important;
  margin-right:0!important;
  
}
#signin-image {
    background-image: url(/project-rayz2007/static/media/rodion-kutsaev-66573-unsplash.b056455f.jpg); 
    max-width: 100%;
    height: 75vh;
    max-height: 400px;
    background-size: cover;

}

#signin-title {
    font-size: 70px;
    padding-top:100px;
    color: rgb(52, 84, 126);
    text-shadow: 3px 3px lightblue;
}


#image {
    background-image: url(/project-rayz2007/static/media/curtis-macnewton-12711-unsplash.0d4a83f5.jpg); 
    max-width: 100%;
    height: 75vh;
    min-height: 250px;
    background-size: cover;

}


p {
    line-height: 200%;
}

h1 {
    padding-top: 10px;
    padding-bottom: 20px;
    text-align: center;
}


#activity-image {
    background-image: url(/project-rayz2007/static/media/guillaume-briard-760548-unsplash.a62cd3dc.jpg); 
    max-width: 100%;
    height: 75vh;
    min-height: 250px;
    /* -webkit-background-size:cover; 
    -moz-background-size: cover;*/
    background-size: cover;

}

div.position-relative.form-group>label {
    padding-top: 20px;
    padding-bottom: 20px;
    font-weight: bold;
    font-size: larger;
    font-style: italic;
}

button.btn.btn-secondary {
    margin-top: 20px;
    
}
#food-image {
    background-image: url(/project-rayz2007/static/media/dan-gold-298710-unsplash.ee5f0dd9.jpg); 
    max-width: 100%;
    height: 75vh;
    min-height: 250px;
    /* -webkit-background-size:cover; 
    -moz-background-size: cover;*/
    background-size: cover;

}

h3 {
    padding-bottom: 10px;
    padding-top: 20px;
    font-style: italic;
}

button.btn.btn-default {
    margin-top: 20px;
    margin-bottom: 20px;
    background-color:#6c757d;
    color: #fff;
    border-color: #6c757d;
}

p {
    padding-top: 20px;
    padding-bottom: 0px;
    margin-bottom: 0px;
}
#save-button {
    margin-top: 20px;
    margin-bottom: 20px;
    background-color:#6c757d;
    color: #fff;
    border-color: #6c757d;
    padding: .375rem .75rem;
    border-radius: .25rem;
    margin-bottom: 0!important;
}

.text>div {
    padding-top: 20px;
    font-weight: bold;
}

div.position-relative.form-check {
    font-weight: lighter;
}
